<template>
  <div class="fix-name">
    <mt-field placeholder="请输入名称" v-model="name"></mt-field>
    <div class="footer-tips">在这里可以设置名称</div>
    <mt-button class="submit-btn save-btn" size="small" type="primary" @click="handUserUpdate">保存</mt-button>
  </div>
</template>
<script>
export default {
  name: 'EditName',
  data () {
    const user = localStorage.getItem('user')
    const name = this.$route.params.name
    return {
      name,
      user: (user && JSON.parse(user)) || {}
    }
  },
  methods: {
    handUserUpdate () {
      this.$socket.updateUser(this.user.id, null, this.name, (res) => {
        if (res.success) {
          localStorage.setItem('user', JSON.stringify(res.user))
        }
      })
    }
  }
}
</script>
<style lang="less">
  .fix-name {
    .mint-cell-wrapper {
      height: 50px;
    }
  }
</style>
